@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap");

/* FONTS */
@font-face {
  font-family: "gingerregular";
  src: url("./assets/Fonts/Ginger/ginger-webfont.woff2") format("woff2"),
    url("./assets/Fonts/Ginger/ginger-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "faktum_testbold";
  src: url("./assets/Fonts/rene_bieder_-_faktum_test_bold-webfont.woff2")
      format("woff2"),
    url("./assets/Fonts/rene_bieder_-_faktum_test_bold-webfont.woff")
      format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "faktum_testregular";
  src: url("./assets/Fonts/rene_bieder_-_faktum_test_regular-webfont.woff2")
      format("woff2"),
    url("./assets/Fonts/rene_bieder_-_faktum_test_regular-webfont.woff")
      format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@import url("https://fonts.googleapis.com/css2?family=SF+Pro+Display:ital,wght@0,400;0,600;0,700;0,800;1,500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap");

body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-sf-pro-display: "SF Pro Display";
  --font-inter: Inter;

  /* font sizes */
  --font-size-xs: 14px;
  --font-size-sm: 16px;
  --font-size-base: 18px;
  --font-size-lg: 20px;
  --font-size-xl: 24px;
  --font-size-2xl: 30px;
  --font-size-3xl: 34px;
  --font-size-4xl: 40px;
  --font-size-5xl: 64px;
  --font-size-6xl: 96px;

  /* Colors */
  --color-white: #fff;
  --color-gray-100: #f8f8f8;
  --color-gray-200: #837f7f;
  --color-gray-300: #1e1e1e;
  --color-gray-400: #111;
  --color-gray-500: #09160f;
  --color-green: #04b256;

  /* Gaps */
  --gap-4xs: 3px;
  --gap-3xs: 4px;
  --gap-2xs: 8px;
  --gap-xs: 16px;
  --gap-sm: 19px;
  --gap-md: 24px;
  --gap-lg: 26px;
  --gap-xl: 32px;
  --gap-2xl: 56px;
  --gap-3xl: 64px;
  --gap-4xl: 128px;

  /* Paddings */
  --padding-3xs: 4px;
  --padding-2xs: 8px;
  --padding-xs: 13px;
  --padding-sm: 20px;
  --padding-md: 26px;
  --padding-lg: 43px;
  --padding-xl: 55px;
  --padding-2xl: 62px;

  /* border radiuses */
  --br-sm: 8px;
  --br-md: 16px;
  --br-lg: 350px;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --font-family-paragraph: "faktum_testregular";
  --font-heading: "gingerregular"
  --background-banner: #e8e0d8;
  --background-intouch: #DBD5C9;
  --background-other: #f16d70;
}

html {
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

::-webkit-scrollbar {
  width: 8px;
  height: 100%;
  transform-origin: center right;
  transition: transform 0.3s, opacity 0.3s;
  opacity: 0;
}

::-webkit-scrollbar-thumb {
  background-color: #444;
  opacity: 0.5;
  width: 7px;
  border-radius: 6px;
  margin: 2px;
  cursor: -webkit-grab;
  cursor: grab;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

::-moz-selection {
  background: #f16d70;
  text-shadow: none;
  color: #fff;
}

::selection {
  background: #f16d70;
  text-shadow: none;
  color: #fff;
}

body {
  overflow-x: hidden;
  text-decoration: none;
  color: #222;
}

img {
  object-fit: contain;
  max-width: 100%;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: aliceblue;
}

a:focus,
a:active,
a:visited {
  color: #eee;
}

h1,
h2,
h3,
h4 {
  font-family: "faktum_testbold";
  font-display: swap;
}

section {
  padding: 100px 80px;
}

p,
span,
button,
a,
input,
textarea {
  font-family: var(--font-family-paragraph);
  font-display: swap;
  font-style: normal;
}

p {
  font-size: 14px;
  font-weight: 600;
  line-height: 24px;
}

button {
  font-size: 14px;
  line-height: 14px;
  cursor: pointer;
  border: none;
  outline: none;
  background: none;
}

.hide-on-desktop {
  display: none;
}

.loader {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
}

@media screen and (max-width: 680px) {
  .hide-on-mobile {
    display: none !important;
  }

  section {
    padding: 50px 20px;
  }

  p {
    line-height: 22px;
    font-size: 13px;
  }
}

@media screen and (max-width: 1000px) and (min-width: 700px) {
  section {
    padding: 50px 20px;
  }

  p {
    line-height: 1.6;
    font-size: 14px;
  }
}



