.banner__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 90px;
    align-items: center;
    justify-content: center;
    /* background-color: #e8e0d8; */
    padding: 190px 50px;
    /* height: 100vh; */
  }
  
  .banner__wrapper h1 {
    font-size: 40px;
    line-height: 1.25;
    text-align: center;
    font-family: "gingerregular";
  }
  
  .banner__buttons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 30px;
  }
  
  .button__description {
    text-align: center;
    border-left: 1px solid #333;
    border-right: 1px solid #333;
    padding: 0 80px;
  }
  
  .banner__btn-first {
    margin-right: 20px;
  }
  
  .banner__btn-second {
    margin-left: 20px;
  }
  .valuePages {
  margin-bottom: 60px;
  display: flex;
  justify-content: space-between;
	box-shadow: 5px 5px 20px 5px #c9c5bc;
  width: 100%px;
  height: 400px;
  align-items: center;
  padding: 10px 40px 0px 40px;
  border-radius:18px
}

  
  @media screen and (max-width: 680px) {
    .valuePages {
      margin-bottom: 60px;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      width: 100%;
      height: 400px;
      align-items: center;
      padding: 10px 40px 0px 40px;
    }
    .banner__wrapper {
      align-items: center;
      justify-content: center;
      padding-bottom: 30px;
    }
  
    .banner__wrapper h1 {
      font-size: 30px;
      line-height: 1.4;
    }
  
    .banner__buttons-mobile {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
  
    .two-buttons-wrap {
      display: flex;
      align-items: center;
      margin-bottom: 20px;
    }
  
    .banner__btn-first-mobile {
      margin-right: 10px;
    }
  }
  
  @media screen and (max-width: 1000px) and (min-width: 700px) {
    .banner__wrapper h1 {
      font-size: 50px;
    }
  }
  
  @media screen and (max-width: 400px) {
    .banner__wrapper h1 {
      font-size: 28px;
    }
  }
  